.part3Container{
    display: flex;
    justify-content: space-around;
    padding: .5em;
    background-color: white;
}

@media screen and (max-width: 1000px) {
    .part3Container{
        flex-direction: column;
        align-items: center;
        gap: 1em;
    }
}


@media screen and (max-width: 450px) {
    .part3Container{
        gap: 1em;
    }
}
