@use '../../index.scss';

.contentInFifthDiv{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1em;
    margin: 0 2em;
    padding: 2em;
}

.voirBtn{
    background-color: index.$blue;
    padding: .5em 2em;
    color: white;
}

.voirBtn:hover{
    color: index.$secondary;
}

.textAndBtnContainer{
    display: flex;
    flex-direction: column;
    gap: .2em;
}

.logoInFifthDiv{
    width: 60%;
}

.roomContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    img{
        // width: 100%;
        height: 70%;
        border-radius: 10px;
    }
}

@media screen and (max-width: 800px) {
    .contentInFifthDiv{
        display: grid;
        grid-template-columns: 1fr;
        padding: .5em;
        gap: 1em;
        margin: 0 2em;
    }
}