@use '../../../../../../index.scss';
.menuIconContainer {
    display: inline-block;
    cursor: pointer;
}

.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: index.$secondary;
    margin: 6px 0;
    transition: 0.4s;
}

.toggleMenuIcon .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
}

.toggleMenuIcon .bar2 {opacity: 0;}

.toggleMenuIcon .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
}

.tousProdsBtn{
    display: flex;
    align-items: center;
    gap: 1em;
    border: 1px solid index.$secondary;
    border-radius: 15px;
    color: index.$secondary;
}