@use '../../../../index.scss';
.part2Container{
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    background-color: index.$blue;
    height: 150px;
}

.logo{
    height: 100%;
}

.searchContainer{
    @include index.center;
    input{
        border-radius: 20px;
        height: 30%;
        width: 100%;
    }
}

.userAndCartIcons{
    @include index.center;
    gap: 2em;
    button{
        font-size: 24px;
        color: white;
    }
    button:hover{
        color: index.$secondary;
    }
}

@media screen and (max-width: 800px) {
    .part2Container{
        grid-template-columns: 1fr 1fr;
    }

    .searchContainer{
        display: none;
    }
}

@media screen and (max-width: 450px) {
    .part2Container{
        grid-template-columns: 5fr .4fr;
    }

    .userAndCartIcons{
        gap: 0;
    }
}