@use '../../../../index.scss';  



.priceContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.currentPrice{
    color: index.$secondary;
    font-weight: bold;
}

.prevPrice{
    text-decoration: line-through;
}

.currentPrice, .prevPrice{
    font-size: 20px;
}

@media screen and (max-width: 500px) {
  .cardInCarousel{
    width: 100%;
  }
}