@use '../../index.scss';

.fstDivBtn{
    background-color: index.$secondary;
    border-radius: 20px;
    color: white!important;
    font-size: larger;
    width: 100%;
}

.firstDivContainer{
    background: url('../../../public/assets/images/first.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
}

.firstDivTxt{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2em;
    color: white;
    gap: 1em;
}

@media screen and (max-width: 800px) {
    .firstDivTxt{
        width: 100%;
    }
}