@use '../../index.scss';

.subBtnContainer{
    display: flex;
    justify-content: center;
    align-items: center;

    .subscribeBtn{
        background-color: index.$secondary;
        color: white;
    }

    .subscribeBtn:hover{
        color: index.$blue;
    }
}


.subscribeInput > input{
    border-radius: 40px;
    width: 100%;
}

.titelo{
    color: white;
    text-align: center;
}

.lastTache{
    background-color: index.$blue;
    padding: 2em 4em;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 2em;
    border-radius: 50px;
    margin: 10em 2em;
    margin-top: 10em;
}

@media screen and (max-width: 1000px) {
    .lastTache{
        display: grid;
        grid-template-columns: 1fr 1fr .5fr;
    }
}

@media screen and (max-width: 700px) {
    .lastTache{
        display: grid;
        margin: 10em 0;
        grid-template-columns: 1fr;
        padding: 1.5em;
    }
}