.imagesContainerThirdDiv{
    display: flex;
    justify-content: space-around;
    margin: 2em .5em;
    div{
        width: 30%;
        img{
            width: 100%;
        border-radius: 20px;
        }
    }
}

@media screen and (max-width: 750px) {
    .imagesContainerThirdDiv{
        flex-direction: column;
        align-items: center;
        gap: 1em;
        div{
            width: 80%;
        }
    }
}